import React, { useState, useEffect } from "react";
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CInvalidFeedback,
  CSpinner,
  CRow,
  CModal,
  CModalFooter,
  CModalBody,
  CModalHeader,
} from "@coreui/react";
import { LogoRasyiidu } from "src/assets/logos";
import CIcon from "@coreui/icons-react";
import request from "src/utils/request";
import endpoints from "src/utils/endpoints";
import { useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import {
  SET_DATA_USER,
  SET_CURRENT_EXAM,
  SET_TOTAL_SOAL,
  RESET_DATA_SOAL,
  RESET_USER_SOAL,
  RESET_DATA_EXAM,
  RESET_USER,
} from "../../../redux/actions";
import ButtonLoading from "../../Loading/ButtonLoading";

const Login = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  // ------------- LOCAL STATE ------------------ //
  const [password, setPassword] = useState("");
  const [username, setUsername] = useState("");
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [errors, setErrors] = useState([]);
  const [alertMessage, setAlertMessage] = useState("");
  const [modal, setModal] = useState(false);

  // ------------- REDUX STATE ------------------ //

  // ------------- METHODS ------------------ //

  const toggle = () => {
    setModal(!modal);
  };

  const validate = () => {
    if (username === "") {
      if (!errors.includes("Username harus diisi"))
        setErrors((state) => [...state, "Username harus diisi"]);
    } else {
      setErrors((state) =>
        state.filter((item) => item !== "Username harus diisi")
      );
    }
    if (password === "") {
      if (!errors.includes("Password harus diisi"))
        setErrors((state) => [...state, "Password harus diisi"]);
    } else {
      setErrors((state) =>
        state.filter((item) => item !== "Password harus diisi")
      );
    }

    if (errors.length) {
      return false;
    }
    return true;
  };

  // -------------------------------------------

  const handleSubmit = async () => {
    setIsLoading(true);
    const isValid = validate();
    if (!isValid) {
      setIsLoading(false);
      return false;
    }
    setIsSubmitted(true);
    if (username && password) {
      const config = {
        ...endpoints.login,
        data: { username: username.trim(), password: password.trim() },
      };
      let response = null;
      try {
        response = await request(null, config);
        localStorage.setItem("token", response.token);
        if (response.status) {
          const { token, profile } = response;
          if (profile.type === 4) {
            let user = await request(null, {
              ...endpoints.getUsers,
              endpoint: endpoints.getUsers.endpoint + `/${profile.id}`,
            });
            const userData = { ...profile, data: user.data };
            dispatch({
              type: SET_DATA_USER,
              payload: {
                token,
                profile: userData,
              },
            });
            getListUjian(user.data.user_participant.user_creator_id);
          } else {
            setAlertMessage("Anda tidak diizinkan masuk");
            setModal(true);
          }
        }
      } catch (error) {
        if (error.status === 409 || error.status === 401) {
          setAlertMessage(error.error.response.data.messages);
          setModal(true);
        } else {
          setAlertMessage(error.error.response.data.messages);
          setModal(true);
        }
        setIsLoading(false);
      }
    } else {
      setIsLoading(false);
    }
  };

  // -------------------------------------------

  const getListUjian = async (user_id_sub_proktor) => {
    // setIsLoading(false);
    // return;
    try {
      const response = await request(
        { user_id_sub_proktor, status_event_ujian: "progress" },
        endpoints.getEvents
      );
      const { status, data: activeExam } = response;
      if (!activeExam.length) {
        setErrors([...errors, "Belum ada ujian yang aktif"]);
        // setModal((state) => !state);
        setIsLoading(false);
        setTimeout(() => {
          setErrors([]);
        }, 3000);
        return;
      }
      setErrors(errors.filter((item) => item !== "Belum ada ujian yang aktif"));
      dispatch({ type: SET_CURRENT_EXAM, payload: activeExam[0] });

      const examDetail = await request(null, {
        ...endpoints.getExamDetail,
        endpoint: endpoints.getExamDetail.endpoint + activeExam[0].exam_id,
      });

      dispatch({
        type: SET_TOTAL_SOAL,
        payload: examDetail.data.jumlah_soal_diujikan,
      });

      setIsLoading(false);
      history.push("/data-confirmation");
    } catch (error) {
      setIsLoading(false);
      setAlertMessage(error.error.response.data.messages);
      setModal(true);
    }
  };

  useEffect(() => {
    const submit = (event) => {
      if (event.code === "Enter") handleSubmit();
    };
    document.addEventListener("keyup", submit);
    return () => document.removeEventListener("keyup", submit);
  });

  useEffect(() => {
    const resetLocalstorage = () => {
      // let curDate = new Date().toLocaleDateString();
      // localStorage.removeItem("persist:root");
      // localStorage.clear();
      // localStorage.setItem("exam-date", curDate);
      dispatch({ type: RESET_USER_SOAL, payload: null });
      dispatch({ type: RESET_DATA_SOAL, payload: null });
      dispatch({ type: RESET_USER, payload: null });
      dispatch({ type: RESET_DATA_EXAM, payload: null });
    };

    resetLocalstorage();
  }, []);

  // -------------------------------------------

  return (
    <div className="c-app c-default-layout flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="8">
            <CCardGroup className="shadow-lg">
              <CCard className="p-4">
                <CCardBody>
                  <CForm>
                    <h1>Login Ujian</h1>
                    <p className="text-muted">
                      Login terlebih dahulu untuk masuk
                    </p>
                    <CInputGroup className="mb-3">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-user" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput
                        type="text"
                        value={username}
                        placeholder="Username"
                        invalid={isSubmitted && username === ""}
                        autoComplete="username"
                        className="form-control-warning error"
                        onChange={(e) => setUsername(e.target.value)}
                      />
                      <CInvalidFeedback className="help-block">
                        Input Username
                      </CInvalidFeedback>
                    </CInputGroup>
                    <CInputGroup className="mb-4">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-lock-locked" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput
                        type="password"
                        value={password}
                        placeholder="Password"
                        invalid={isSubmitted && password === ""}
                        autoComplete="current-password"
                        onChange={(e) => setPassword(e.target.value)}
                      />
                      <CInvalidFeedback className="help-block">
                        Input Password
                      </CInvalidFeedback>
                    </CInputGroup>
                    <CRow>
                      <CCol xs="6">
                        <CButton
                          color="primary"
                          className="px-4"
                          disabled={isLoading}
                          onClick={() => handleSubmit()}
                        >
                          {isLoading && <CSpinner grow size="sm" />}Login
                        </CButton>
                      </CCol>
                      <CCol xs="6" className="text-right">
                        {/* <CButton color="link" className="px-0">
                          Forgot password?
                        </CButton> */}
                      </CCol>
                    </CRow>
                    <CRow>
                      <ul className="text-center">
                        {errors.map((item, index) => {
                          return (
                            <li className="text-danger" key={index}>
                              {item}
                            </li>
                          );
                        })}
                      </ul>
                    </CRow>
                  </CForm>
                </CCardBody>
              </CCard>
              <CCard
                className="text-black bg-white py-5 d-md-down-none"
                style={{ width: "44%" }}
              >
                <CCardBody className="d-flex justify-content-center align-items-center">
                  <LogoRasyiidu height={80} />
                </CCardBody>
              </CCard>
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer>
      <CModal show={modal} onClose={toggle} closeOnBackdrop={false}>
        <CModalHeader closeButton>Informasi</CModalHeader>
        <CModalBody>{alertMessage}</CModalBody>
        <CModalFooter>
          <CButton color="primary" onClick={toggle}>
            OK
          </CButton>
        </CModalFooter>
      </CModal>
    </div>
  );
};

export default Login;
